import "core-js/modules/es.array.for-each";
import "core-js/modules/es.array.index-of";
import "core-js/modules/es.string.link";
import "core-js/modules/web.dom-collections.for-each";
//
//
//
//
//
//
//
//
//
//
export default {
  name: 'About_Details_Left',
  data: function data() {
    return {
      AboutList: [{
        name: "公司介绍",
        link: "/About_Details",
        index: 0
      }, {
        name: "集团介绍",
        link: "/About_Details/About_Group_Introduce",
        index: 1
      }, {
        name: "企业文化",
        link: "/About_Details/About_Company_Culture",
        index: 2
      }, {
        name: "企业愿景",
        link: "/About_Details/About_Company_Wish",
        index: 3
      }],
      selectNav: 0
    };
  },
  created: function created() {},
  mounted: function mounted() {
    this.activeChange();
  },
  watch: {
    // 监听路由的变化，来显示文档标题和导航栏高亮
    $route: function $route(to, from) {
      this.activeChange();
    }
  },
  methods: {
    aboutNav: function aboutNav(item) {
      this.$router.push({
        path: item.link
      });
    },
    activeChange: function activeChange() {
      var _this = this;

      var routeChoice = this.$route.path;
      this.AboutList.forEach(function (item, i) {
        if (routeChoice.indexOf(item.link) > -1) {
          _this.selectNav = item.index;
        }
      });
    }
  }
};