import "@/assets/images/iconfont/iconfont.css";
export default {
  name: 'About',
  created: function created() {},
  methods: {
    ScrollTop: function ScrollTop(number, time) {
      var _this = this;

      if (!time) {
        document.body.scrollTop = document.documentElement.scrollTop = number;
        return number;
      }

      var spacingTime = 20; // 设置循环的间隔时间  值越小消耗性能越高

      var spacingInex = time / spacingTime; // 计算循环的次数

      var nowTop = document.body.scrollTop + document.documentElement.scrollTop; // 获取当前滚动条位置

      var everTop = (number - nowTop) / spacingInex; // 计算每次滑动的距离

      var scrollTimer = setInterval(function () {
        if (spacingInex > 0) {
          spacingInex--;

          _this.ScrollTop(nowTop += everTop);
        } else {
          clearInterval(scrollTimer); // 清除计时器
        }
      }, spacingTime);
    },
    linkCustomSesrvice: function linkCustomSesrvice() {
      location.href = 'https://kf.hdfax.com:8443/IM/login/wzlogin.do';
    }
  },
  mounted: function mounted() {
    var hash = window.location.hash;
    var index = hash.lastIndexOf("#");

    if (index != -1) {
      var id = hash.substring(index + 1, hash.length + 1);
      var div = document.getElementById(id);

      if (div) {
        var top = div.offsetTop - 130;
        this.ScrollTop(top, 600);
      }
    }
  }
};